import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';

import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';

import 'rxjs/add/operator/debounceTime';

import { FormControl } from '@angular/forms';

import { Subject } from 'rxjs/Subject'

  interface Items {
    description: string;
    itemid: number;
  }

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})


export class HomePage implements OnInit{
  [x: string]: any;

  itemsCollection: AngularFirestoreCollection<Items>; //Firestore collection
	items: Observable<Items[]>; // read collection

  searchTerm: string;
  
   startAt = new Subject();
   endAt = new Subject();
  
   clubs;
   allclubs;
  
   startobs = this.startAt.asObservable();
   endobs = this.endAt.asObservable();

  searchControl: FormControl;
  
  constructor(public navCtrl: NavController,
     private afs: AngularFirestore) {

      this.searching = false;
      this.searchControl = new FormControl();
      this.empty = true;
  }

  ngOnInit() {
    this.getallclubs().subscribe((clubs) => {
      this.allclubs = clubs;
    })
    Observable.combineLatest(this.startobs, this.endobs).subscribe((value) => {
      this.firequery(value[0], value[1]).subscribe((clubs) => {
        this.clubs = clubs;
      })
    })
  }

  ionViewDidLoad() {
    
           this.searchControl.valueChanges.debounceTime(700).subscribe(search => {
    
            //   this.setFilteredItems();
            this.searching = false;
            this.empty = false;
           });
       }

  getItems($event){
  
    this.searching = true;
    this.empty = false;

   let q = $event.target.value;

    if (q != '') {
      this.startAt.next(q);
      this.endAt.next(q + "\uf8ff");
    }
    else {
      // this.empty = true;
       this.clubs = this.allclubs;
    }
  }
 
  firequery(start, end) {
     return this.afs.collection('items', ref => ref.orderBy('name').startAt(start).endAt(end)).valueChanges();
  }
 
  getallclubs() {
    return this.afs.collection('items', ref => ref.limit(10)).valueChanges();
     // return this.afs.collection('items', ref => ref.orderBy('name').limit(10)).valueChanges();    
  }
   

  dismiss() {
    this.viewCtrl.dismiss();
  }


  private isOn: boolean = false;
  
    getButtonText(): string {
      return `Switch ${ this.isOn ? 'Off' : 'On' }`;
    }
    setState(): void {
      this.isOn = !this.isOn;
    }
  
  toggleDetails() {
      this.isOn = !this.isOn;
    }
  
  row(item){
    console.log("Clicked");
  }
 
  addItem(){
    this.ionViewElement();
  	this.itemsCollection.add({
      description: "1TB Space with great performance",
      itemid: 13423
    })
      .then( (result) => {
        console.log("Document added with id >>> ", result.id);
      })
      .catch( (error) => {
        console.error("Error adding document: ", error);
      });
  }

  about(){
    this.navCtrl.push("AboutPage");
  }
}



