import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';

import { OneSignal } from '@ionic-native/onesignal';

import { BackgroundMode } from '@ionic-native/background-mode';

import { ToastController, Toast } from 'ionic-angular';

import { Network } from '@ionic-native/network';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = HomePage;

  constructor(platform: Platform, statusBar: StatusBar, 
    splashScreen: SplashScreen,
    private oneSignal: OneSignal,
    private backgroundMode: BackgroundMode,
    private _toastCtrl: ToastController, 
    private _network: Network) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();

      this.backgroundMode.enable();

      this.oneSignal.startInit('fab9d1d6-4b03-4fff-9162-c6e6649286aa', '922319694867');
      
      this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.InAppAlert);
      
      this.oneSignal.handleNotificationReceived().subscribe(() => {
       // do something when notification is received
      //  alert("Receievd");
      });
      
      this.oneSignal.handleNotificationOpened().subscribe(() => {
        // do something when a notification is opened
        // alert("Hai");
      });
      
      this.oneSignal.endInit();

    });

    let noInternetToast: Toast;
    let onlineToast: Toast;

    this._network.onDisconnect().subscribe(() => {

      noInternetToast = this._toastCtrl.create({
        message: 'No Internet Connection',
        position: 'bottom',
        duration: 3000,
        cssClass: 'failure'
      });
      noInternetToast.present();
    });

    this._network.onConnect().subscribe(() => {
      onlineToast = this._toastCtrl.create({
        message: 'Connected to Internet',
        position: 'bottom',
        duration: 2000,
        cssClass: 'success'
      });
      // noInternetToast.dismiss();
      onlineToast.present();
    });
  }
}




